<template>
  <n-space vertical align="center">
    <n-grid :cols="12" style="width: 80%">
      <n-grid-item :span="12">
        <img src="ngram.png" alt="logo"/>
      </n-grid-item>

      <n-grid-item :span="12">
        <div class="items-center">
          <n-auto-complete
              placeholder="Введите запрос"
              :options="autoCompleteOptions"
              :loading="autoCompleteLoading"
              :on-update:value="handleAutocompleteUpdate"
              :render-label="renderLabel"
          />
        </div>
      </n-grid-item>

      <n-grid-item class="mt-2" :span="12">
        <div class="items-center">
          <Spellcheck v-model:text="textRef"/>
        </div>
      </n-grid-item>

      <n-grid-item class="mt-2" :span="12">
        <div class="items-center">
          <n-button @click="modelSetupRef.showModal()">
            <template #icon>
              <n-icon>
                <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 512 512" enable-background="new 0 0 512 512" xml:space="preserve"><path d="M416.3,256c0-21,13.1-38.9,31.7-46.1c-4.9-20.5-13-39.7-23.7-57.1c-6.4,2.8-13.2,4.3-20.1,4.3c-12.6,0-25.2-4.8-34.9-14.4
	c-14.9-14.9-18.2-36.8-10.2-55C341.8,77,322.5,68.9,302.1,64C295,82.5,277,95.7,256,95.7c-21,0-39-13.2-46.1-31.7
	c-20.5,4.9-39.7,13-57.1,23.7c8.1,18.1,4.7,40.1-10.2,55c-9.6,9.6-22.3,14.4-34.9,14.4c-6.9,0-13.7-1.4-20.1-4.3
	C77,170.3,68.9,189.5,64,210c18.5,7.1,31.7,25,31.7,46.1c0,21-13.1,38.9-31.6,46.1c4.9,20.5,13,39.7,23.7,57.1
	c6.4-2.8,13.2-4.2,20-4.2c12.6,0,25.2,4.8,34.9,14.4c14.8,14.8,18.2,36.8,10.2,54.9c17.4,10.7,36.7,18.8,57.1,23.7
	c7.1-18.5,25-31.6,46-31.6c21,0,38.9,13.1,46,31.6c20.5-4.9,39.7-13,57.1-23.7c-8-18.1-4.6-40,10.2-54.9
	c9.6-9.6,22.2-14.4,34.9-14.4c6.8,0,13.7,1.4,20,4.2c10.7-17.4,18.8-36.7,23.7-57.1C429.5,295,416.3,277.1,416.3,256z M256.9,335.9
	c-44.3,0-80-35.9-80-80c0-44.1,35.7-80,80-80s80,35.9,80,80C336.9,300,301.2,335.9,256.9,335.9z"></path></svg>
              </n-icon>
            </template>
            Модель
          </n-button>
        </div>
      </n-grid-item>
    </n-grid>

  </n-space>

  <ModelSetupModalComponent ref="modelSetupRef"></ModelSetupModalComponent>
</template>

<script>
import {
  NCard,
  NH5,
  NText,
  NUpload,
  NSpace,
  NButton,
  NInput,
  NGrid,
  NGridItem,
  NAutoComplete,
  NTag,
  NIcon
} from "naive-ui";
import ModelSetupModalComponent from "@/components/ModelSetupModalComponent";
import {h, inject, ref} from "vue";
import axios from "axios";
import Spellcheck from "@/components/Spellcheck";

export default {
  name: 'HelloWorld',
  components: {
    Spellcheck,
    ModelSetupModalComponent,
    NCard, NH5, NText, NUpload, NSpace, NButton, NInput, NGrid, NGridItem, NAutoComplete, NIcon
  },
  setup() {
    const defaultURL = inject('defaultURL')

    const textRef = ref("")
    const modelSetupRef = ref(null)

    const autoCompleteOptions = ref([])
    const autoCompleteLoading = ref(false)

    async function handleAutocompleteUpdate(text) {
      textRef.value = text
      if (!text) {
        autoCompleteOptions.value = []
        return
      }
      autoCompleteLoading.value = true

      try {
        const res = await axios.get(`${defaultURL}/api/getAutocomplete`, {params: {
            start_string: text.toLowerCase()
          }})
        autoCompleteOptions.value = res.data
        console.log(res.data)
      } finally {
        autoCompleteLoading.value = false
      }
    }

    function renderLabel(option) {
      return [
        option[0].join(' '),
        " ",
        h(NTag, { size: "small", type: "info" }, { default: () => option[1].toString().substr(0, 6) })
      ]

    }

    return {
      modelSetupRef, autoCompleteOptions, autoCompleteLoading, handleAutocompleteUpdate, renderLabel, textRef
    }
  }
}
</script>
