<template>
  <n-button :loading="loading" @click="spellcheck">
    <template #icon>
      <n-icon>
        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 24 24"><g fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><path d="M4 20h4L18.5 9.5a1.5 1.5 0 0 0-4-4L4 16v4"></path><path d="M13.5 6.5l4 4"></path></g></svg>
      </n-icon>
    </template>
    Spellcheck
  </n-button>
  <n-collapse-transition :show="showResults">
    <n-space vertical>
      <n-p v-html="result[0].join(' ')" v-for="result in results"></n-p>
    </n-space>
  </n-collapse-transition>
</template>

<script>
import {
  NAutoComplete,
  NButton,
  NCard,
  NCollapseTransition,
  NGrid,
  NGridItem,
  NH5, NIcon,
  NInput, NList, NP,
  NSpace,
  NText,
  NUpload
} from "naive-ui";
import {inject, ref} from "vue";
import axios from "axios";

export default {
  name: "Spellcheck",
  props: ['text'],
  components: {
    NCard, NH5, NP, NText, NUpload, NSpace, NButton, NInput, NGrid, NGridItem, NAutoComplete, NCollapseTransition,
    NList, NIcon
  },
  setup(props) {
    const defaultURL = inject('defaultURL')

    const loading = ref(false)
    const results = ref([])
    const showResults = ref(false)

    async function spellcheck() {
      loading.value = true
      try {
        const res = await axios.get(`${defaultURL}/api/spellcheck`, {params: {
            q: props.text.toLowerCase()
          }})
        results.value = res.data
        showResults.value = true
      } finally {
        loading.value = false
      }
    }
    return {
      loading, results, showResults, spellcheck
    }
  }
}
</script>

